import React, { Component } from 'react';

import './image.css';

export class Image extends Component {
    static displayName = Image.name;

    constructor(props) {
        super(props);
        this.state = {
            id: 0,
            masterId: 0,
            selected: false,
            imageUrl: "",
            name: "",
            clickable: false, 
            bundle: false,
            showHidden: false
        };

        this.clicked = this.clicked.bind(this);
        this.hoverFunction = this.hoverFunction.bind(this);
        this.hoverLeave = this.hoverLeave.bind(this);
    }
    
    componentDidMount(){
        this.setState({imageUrl: this.props.imageUrl});
        this.setState({name: this.props.name});
        this.setState({clickable: this.props.clickable});
        this.setState({id: this.props.id});
        this.setState({selected: this.props.clicked});
        this.setState({masterId: this.props.masterId});
        var str = this.props.name;
        var n = str.includes("Bundle");
        if(n){
            this.setState({bundle: true});
        }
    }

    clicked() {
        if(this.state.clickable){
            if(this.state.selected === true){
                this.setState({selected: false}); 
                this.props.saveClicked(this.state.masterId, this.state.id, false);
            }
            else{
                this.setState({selected: true}); 
                this.props.saveClicked(this.state.masterId, this.state.id, true);
            }
        }        
    }

    hoverFunction(){
        if(this.state.showHidden !== true){
            this.setState({showHidden: true});
        }
        this.setState({})
    }
    hoverLeave(){
        if(this.state.showHidden !== false){
            this.setState({showHidden: false});
        }
    }
    
    render() {
        const {selected, bundle} = this.state;
        
        const {where } = this.props;
        let hiddenText = <div></div>;
        if(this.state.showHidden){
            hiddenText = <div className="hidden">{where}</div>;
        }

        if(bundle){
            return (
                <div className="bundbox bundle">            
                     <img className="mynd" alt="" src={this.state.imageUrl}/>{this.state.name}
                </div>
                );
        }
        else{
            return (
                <div className={`box ${selected ? "clicked" : ""}`} onClick={this.clicked} onMouseOver={this.hoverFunction} onMouseLeave={this.hoverLeave}>            
                    <img className="mynd" alt="" src={this.state.imageUrl}/> {this.state.name}
                    {hiddenText}
                </div>
                ); 
        }
        
    };
}

export default Image;